import React, { Component } from 'react';
import { SketchPicker } from 'react-color';
import { withFirebase } from '../Firebase';
import { compose } from 'recompose';
import './index.css';
import DeleteIcon from '@material-ui/icons/Delete';

class SelectCategory extends Component {
  constructor(props) {
    super(props);

    this.state = {
      setCategories: [],
      name: '',
      categoryName: '',
      category: {},
      color: '#EF463B ',
      sendCategory: []
    };

    // this.removeCategory = this.removeCategory.bind(this);
    this.passCategories = this.passCategories.bind(this);
  }

  submitCategories = e => {
    e.preventDefault();
    let categoryObj = {};

    let sendCategory = {};

    categoryObj.color = this.state.color;
    categoryObj.name = this.state.name;

    // SendCategory sends category values up to parent
    sendCategory.color = this.state.color;
    sendCategory.name = this.state.name;
    sendCategory.selected = false;
    this.setState({
      setCategories: [...this.state.setCategories, categoryObj],
      sendCategory: [...this.state.sendCategory, sendCategory]
    });
  };

  handleChangeComplete = color => {
    this.setState({ color: color.hex });
  };

  selectCategory = e => {
    this.setState({
      name: e.target.value
    });
  };

  removeCategory = event => {
    let index = event.target.getAttribute('data-index');
    let setCategories = [...this.state.setCategories];
    setCategories.splice(index, 1);
    this.setState({ setCategories });
  };

  passCategories = e => {
    e.preventDefault();
    this.props.getCategories(this.state.setCategories, this.state.sendCategory);
  };

  render() {
    let categoryList = this.state.setCategories.map((item, i) => {
      let categoryStyle = {
        background: item.color
      };
      console.log('catt list', this.s);
      return (
        <li key={i} className="category-list-item">
          <button onClick={this.removeCategory} data-index={i}>
            <DeleteIcon className="delete-category" />
          </button>
          <div className="d-flex align-items-center">
            <div className="hex-color" style={categoryStyle}>
              {item.name}
            </div>
          </div>
        </li>
      );
    });
    return (
      <React.Fragment>
        <form onSubmit={this.passCategories}>
          <input
            type="text"
            onChange={this.selectCategory.bind(this)}
            class="light-input"
            placeholder="CATEGORY_NAME"
          />
          <SketchPicker
            color={this.state.color}
            onChangeComplete={this.handleChangeComplete.bind(this)}
          />
          <div class="d-flex flex-column justify-content align-items-center">
            <ul id="selected-categories">{categoryList}</ul>
            <button onClick={this.submitCategories} class="gen-red-btn">
              Add Categories
            </button>
            <br />
            <button onClick={this.passCategories} class="gen-red-btn">
              Submit
            </button>
          </div>
        </form>
      </React.Fragment>
    );
  }
}

export default compose(withFirebase(SelectCategory));
